import Image from "next/image"

export default function AboutPage() {
  return (
    <main className="pt-28">

      {/* HERO */}
      <section className="container mx-auto text-center mb-20">
        <h1 className="text-5xl font-bold mb-6">About Us</h1>
        <p className="text-lg text-gray-600 max-w-2xl mx-auto">
          We help brands grow through strategy, creativity, and superior digital craftsmanship.
        </p>
      </section>

      {/* BLOCK 1 — Story */}
      <section className="bg-orange-50 py-16">
        <div className="container mx-auto grid md:grid-cols-2 gap-10 items-center">

          {/* Text */}
          <div>
            <h2 className="text-3xl font-bold mb-4">Who We Are</h2>
            <p className="text-gray-700 leading-relaxed">
              Venturemond is a full-stack digital agency focused on building modern, scalable,
              and meaningful digital products. We combine design thinking, strategy, and 
              engineering excellence to create websites, apps, and brand experiences that convert.
            </p>
          </div>

          {/* Image */}
          <div className="relative h-72 w-full rounded-xl overflow-hidden shadow-lg">
            <Image
              src="/images/about/about1.jpg"
              alt="About Us"
              fill
              className="object-cover"
            />
          </div>

        </div>
      </section>

      {/* BLOCK 2 — Mission */}
      <section className="container mx-auto py-20 grid md:grid-cols-2 gap-10 items-center">

        {/* Image */}
        <div className="relative h-72 w-full rounded-xl overflow-hidden shadow-lg">
          <Image
            src="/images/about/about2.jpg"
            alt="Mission Image"
            fill
            className="object-cover"
          />
        </div>

        {/* Text */}
        <div>
          <h2 className="text-3xl font-bold mb-4">Our Mission</h2>
          <p className="text-gray-700 leading-relaxed">
            Our mission is simple: create digital experiences that help brands grow and
            create real impact. Whether it’s a startup building its first product or 
            a company scaling its digital presence — we build solutions that last.
          </p>
        </div>

      </section>

      {/* BLOCK 3 — Culture Image Only */}
      <section className="py-20 bg-slate-100">
        <div className="container mx-auto text-center">
          <h2 className="text-3xl font-bold mb-6">Our Culture</h2>
          <p className="text-gray-600 max-w-2xl mx-auto">
            We believe great work happens in an environment where creativity and collaboration thrive.
          </p>

          <div className="mt-10">
            <div className="relative h-72 w-full md:w-[70%] mx-auto rounded-xl overflow-hidden shadow-lg">
              <Image
                src="/images/about/about3.jpg"
                alt="Team Culture"
                fill
                className="object-cover"
              />
            </div>
          </div>
        </div>
      </section>

    </main>
  )
}
